import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, Validators, FormGroup, FormControl, FormArray } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { FileUploader } from 'ng2-file-upload';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import * as MSGraph from "microsoft-graph";

import { Student, Analysis } from '../../../Models/student.model'
@Component({
    templateUrl: './student.detail.cmp.html',
    styleUrls: ['./student.detail.cmp.scss']
})
export class StudentDetailCmp implements OnInit, OnDestroy {


    my_form: FormGroup;
    instructors = [];
    goals = [];
    studentData: any = {};
    isNewAnalysis: boolean;
    chartBase64:string ="";
    public uploader: FileUploader = new FileUploader({});

    subscription_register_instructors: Subscription;
    subscription_get_student_data: Subscription;
    subscription_get_analysis_data: Subscription;
    subscription_get_chart_image: Subscription;
    constructor(private _fb: FormBuilder, private _route: ActivatedRoute, private _broadcaster: Broadcaster, private _DomSanitizationService: DomSanitizer, private _dataRouting: DataRouting) {
    }

    ngOnInit() {

        this.uploader.onAfterAddingFile = this.selectPhoto.bind(this);
        let id = (this._route.snapshot.params['id']);
        if (id) {
            // this.selectedUser = this._dataRouting.getUser(id);
        }
        else {
            this.studentData.url = 'assets/libs/download.jpeg';
        }
        this.registerEvents();

        this.my_form = this._fb.group({
            instructor: ['', Validators.required],
            age: ['', Validators.required],
            goal: ['', Validators.required],
            height: ['', Validators.required],
            analysiss: this._fb.array([
                // this.initAnalysis()
            ])
        })
        this.instructors = this._dataRouting.instructorsTest;
        this.goals = this._dataRouting.goals;
    }

    initAnalysis(data: any) {
        if (data) {
            return this._fb.group({
                month: [data.month, Validators.required],
                weight: [data.weight, Validators.required],
                fat: [data.fat, Validators.required],
            });
        }
        else {
            return this._fb.group({
                month: ['', Validators.required],
                weight: ['', Validators.required],
                fat: ['', Validators.required],
            });
        }

    }

    addAnalysis(data: any): FormGroup {
        const control = <FormArray>this.my_form.controls['analysiss'];
        this.isNewAnalysis = true;
        let newAnaylysis: FormGroup = this.initAnalysis(data);
        control.push(this.initAnalysis(data));
        return newAnaylysis;
    }

    removeAnalysis(i: number) {
        const control = <FormArray>this.my_form.controls['analysiss'];
        control.removeAt(i);
    }

    ngOnDestroy() {
        this.unRegisterEvents();
    }

    registerEvents() {
        this.subscription_register_instructors = this._broadcaster.on<string>('instructorReady')
            .subscribe((photoObj: any) => {
                // this.instructors = this._dataRouting.instructors;
                // this.instructors.map((ins)=>{
                //    this.instructorsTest.push({givenName:ins.givenName,displayName:ins.displayName})     
                // })
            })
        this.subscription_get_student_data = this._broadcaster.on<string>('get_student_data')
            .subscribe((result: any) => {
                this.studentData = this._dataRouting.currentStudent;
                if (this.studentData) {
                    (<FormControl>this.my_form.controls['instructor']).setValue(this.studentData[1], { onlySelf: true });
                    (<FormControl>this.my_form.controls['age']).setValue(this.studentData[2], { onlySelf: true });
                    (<FormControl>this.my_form.controls['goal']).setValue(this.studentData[3], { onlySelf: true });
                    (<FormControl>this.my_form.controls['height']).setValue(this.studentData[4], { onlySelf: true });
                }
            })
        this.subscription_get_analysis_data = this._broadcaster.on<string>('get_analysis_data')
            .subscribe((result: any) => {
                if (this._dataRouting.currentAnalysis) {
                    this._dataRouting.currentAnalysis.map((item: any, index: number) => {
                        if (index !== 0) {
                            this.addAnalysis({ "month": item[1], "weight": item[2], "fat": item[3] });
                        }
                    })
                }
            })

            this.subscription_get_chart_image = this._broadcaster.on<string>('get_chart_image')
            .subscribe((result: any) => {
                if (this._dataRouting.chartBase64) {
                   this.chartBase64 = "data:image/png;base64," + this._dataRouting.chartBase64;
                }
            })
    }

    unRegisterEvents() {
        this.subscription_register_instructors.unsubscribe();
        this.subscription_get_student_data.unsubscribe();
        this.subscription_get_analysis_data.unsubscribe();
        this.subscription_get_chart_image.unsubscribe();
    }

    selectPhoto(fileItem: any) {

        let that = this;
        var reader = new FileReader();
        reader.addEventListener("load", (e: any) => {
            this.studentData.url = e.target.result
        }, false);
        if (fileItem && fileItem.some.type.match('image.*')) {
            reader.readAsDataURL(fileItem.some);
        }
    }
    createChart() {
        let event_data: any = {};
        event_data.action = 'chart';
        event_data.url = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet3/charts/add';
        event_data.http_data = {
            "type": "ColumnStacked",
            "sourceData": "A1:E5",
            "seriesBy": "Auto"
        }
        event_data.filtered_url = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet3/charts('" + 'Chart 1' + "')/Image(width=0,height=0,fittingMode='fit')";
        this._dataRouting.uiEvents2(event_data);
    }

    submit() {
        let event_data: any = {};
        if (this.studentData) {
            let row_index = +this.studentData[5] - 2;
            event_data.action = 'student_update';
            event_data.url = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/rows/itemAt(index=' + row_index + ')';
            event_data.http_data = { "values": [[this._dataRouting.currentUser.userPrincipalName, this.my_form.value.instructor, this.my_form.value.age, this.my_form.value.goal, this.my_form.value.height, '=Row()']] };
            event_data.temp_data = { "values": [[this._dataRouting.currentUser.userPrincipalName, this.my_form.value.instructor, this.my_form.value.age, this.my_form.value.goal, this.my_form.value.height, this.studentData[6]]] }
            this._dataRouting.uiEvents2(event_data);
        }
        else {
            event_data.action = 'student_add';
            event_data.url = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/rows/add";
            event_data.http_data = {
                "values": [[this._dataRouting.currentUser.userPrincipalName, this.my_form.value.instructor, this.my_form.value.age, this.my_form.value.goal, this.my_form.value.height, '=Row()']]
            }
            this._dataRouting.uiEvents2(event_data);
        }
        if (this.isNewAnalysis) {
            // let analysiss: any = (<FormArray>this.my_form.controls['analysiss']).controls[(<FormArray>this.my_form.controls['analysiss']).controls.length];

            event_data.action = 'student_add';
            event_data.url = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet3/tables/5/rows/add";
            event_data.http_data = {
                "values": [[this._dataRouting.currentUser.userPrincipalName,
                this.my_form.value.analysiss[this.my_form.value.analysiss.length - 1].month,
                this.my_form.value.analysiss[this.my_form.value.analysiss.length - 1].weight,
                this.my_form.value.analysiss[this.my_form.value.analysiss.length - 1].fat,
                    '=Row()']]
            }
            this._dataRouting.uiEvents2(event_data);
        }
    }
}




