import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Router, NavigationEnd, NavigationStart, RoutesRecognized } from "@angular/router"
import { DomSanitizer } from '@angular/platform-browser';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import * as MSGraph from "microsoft-graph"
import { Subscription } from 'rxjs/Subscription';

@Component({
  templateUrl: './students.cmp.html',
  styleUrls: ['./students.cmp.scss']
})
export class StudentsCmp implements OnInit, OnDestroy {

  activeTabIndex = 0;
  dynamicTabs = [
    {
      label: 'All'
    },
    {
      label: 'Ayush Jaiswal'
    },
    {
      label: 'Ben Halverson'
    },
    {
      label: 'Mayank Arya'
    }
  ];
  tempUrl: string = 'assets/libs/download.jpeg';
  students: any[] = [];
  subscription_get_students: Subscription;
  constructor(private _router: Router, private _dataRouting: DataRouting, private _broadcaster: Broadcaster, private _DomSanitizationService: DomSanitizer) {
  }
  ngOnInit() {
    this.registerEvents();
  }
  ngOnDestroy() {
    this.unRegisterBroadcast();
  }
  registerEvents() {
    this.subscription_get_students = this._broadcaster.on<string>('get_students')
      .subscribe((result: any) => {
        this.students = result;
      })
  }
  unRegisterBroadcast() {
    this.subscription_get_students.unsubscribe();
  }

  tabChanged() {
    let event_data: any = {};
    if(this.activeTabIndex === 0)
    {
      event_data.action = 'All';
      this._dataRouting.uiEvents2(event_data);  
      return;
    }
    event_data.action = 'filter';
    event_data.url = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/columns(id='" + 2 + "')/filter/apply";
    event_data.http_data = {
      "criteria": {
        "filterOn": "custom",
        "criterion1": "=" + this.dynamicTabs[this.activeTabIndex].label + ""
      }
    };
    event_data.filtered_url = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/range/visibleView';
    this._dataRouting.uiEvents2(event_data);
  }

  gotoUser(id) {
    this._router.navigate(['/', id]);
  }
}


