import * as MSGraph from "microsoft-graph"
import { InsideOffice } from "../../../../app/Modules/shared/utility/officeEnv"
import { Subscription } from 'rxjs/Subscription';
import { Filter_Data, Filter_Params } from '../Models/student.model'
@Injectable()
export class DataRouting {

    //data
    instructorsTest = [
        { givenName: 'Mayank Arya', displayName: 'Mayank Arya' },
        { givenName: 'Ben Halverson', displayName: 'Ben Halverson' },
        { givenName: 'Ayush Jaiswal', displayName: 'Ayush Jaiswal' }
    ];
    goals = [
        { id: 'weightLoss', test: 'Weight Loss' },
        { id: 'weightGain', test: 'Weight Gain' }
    ];
    persistsId: string;
    nonpersistId: string;
    navItems = []
    instructors: any[] = [];
    students: any[] = [];
    currentUser: MSGraph.User = {};
    studentSchema: any[];
    currentStudent: any = {};
    currentAnalysis: any = {};
    excelData: any[] = [];
    chartBase64:string = "";
    //events
    subscription_login_success: Subscription;
    subscription_profile_photo: Subscription;
    //


    constructor(private _router: Router, private _broadcaster: Broadcaster, private _o365SecurityService: O365SecurityService) {
        this.registerRoutingEvents();
        this.registerEvents();
    }

    registerRoutingEvents() {
        this._router.events.filter(e => e instanceof NavigationEnd).subscribe(e => {
            this._broadcaster.broadcast("spinner_on", true);
            if (e.url.includes("home")) {
                //change to merge
                if (Object.keys(this.currentUser).length !== 0) {
                    this._broadcaster.broadcast("spinner_on", false);
                    return;
                }
                let obs_Me = this._o365SecurityService.get(this.graphConstat.me, "", "", "", "");
                let obs_persist_session = this._o365SecurityService.post(this.graphConstat.excel_create_session, { "persistChanges": true });
                let obs_non_persist_session = this._o365SecurityService.post(this.graphConstat.excel_create_session, { "persistChanges": false })
                obs_Me.concat(obs_persist_session).concat(obs_non_persist_session).map((response: any, index: number) => {
                    this.parseHomeResponse(response, index);
                    if (index == 2) this._broadcaster.broadcast("spinner_on", false);
                }).subscribe((response: any) => {
                })
            }
            else if (e.url.includes("instructors")) {
                let obs_instructors = this._o365SecurityService.get(this.graphConstat.users, undefined, undefined, "jobTitle eq 'instructor'");
                obs_instructors.subscribe((response: any) => {
                    this.instructors = response.value;
                    this._broadcaster.broadcast("instructorReady", this.instructors);
                    this._broadcaster.broadcast("spinner_on", false);
                })
            }
            else if (e.url.includes("register")) {
                if (Object.keys(this.currentStudent).length !== 0) {
                    this._broadcaster.broadcast("instructorReady", this.instructors);
                    this._broadcaster.broadcast("get_student_data", this.currentStudent);
                    this._broadcaster.broadcast("spinner_on", false);
                    return;
                }
                let objO365 = this._o365SecurityService;
                let filter_params_student: Filter_Params = { worksheet_index: "2", table_index: "1", column_index: "1" }
                let filter_data_students = this.getFilterData(filter_params_student);
                let filter_params_analysis: Filter_Params = { worksheet_index: "3", table_index: "5", column_index: "1" }
                let filter_data_analysis = this.getFilterData(filter_params_analysis);
                Observable.forkJoin([objO365.get(this.graphConstat.users, undefined, undefined, "jobTitle eq 'instructor'"),
                this._o365SecurityService.post(filter_data_students.filter_url, filter_data_students.filter_criteria, this.nonpersistId),
                this._o365SecurityService.post(filter_data_analysis.filter_url, filter_data_analysis.filter_criteria, this.nonpersistId),
                ]).subscribe((response) => {
                    this.instructors = response[0].value;
                    this._broadcaster.broadcast("instructorReady", this.instructors);
                    if (response[1] === null) {
                        objO365.get(filter_data_students.visiable_url, "", "", "", this.nonpersistId).subscribe((response: any) => {
                            this.studentSchema = response.values[0];
                            this.currentStudent = response.values[1];
                            this._broadcaster.broadcast("get_student_data", this.currentStudent);
                        })
                    }
                    if (response[2] === null) {
                        objO365.get(filter_data_analysis.visiable_url, "", "", "", this.nonpersistId).subscribe((response: any) => {
                            if (response.values.length === 1) return;
                            this.studentSchema = response.values[0];
                            this.currentAnalysis = response.text;
                            this._broadcaster.broadcast("get_analysis_data", this.currentStudent);
                            this._broadcaster.broadcast("spinner_on", false);
                        })
                    }
                }, (e) => {

                }, () => {
                    this._broadcaster.broadcast("spinner_on", false);
                })
            }
            else if (e.url.includes("students")) {
                let excelUrl1 = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/range/visibleView';
                this._o365SecurityService.get(excelUrl1, "", "", "", this.persistsId).subscribe((response: any) => {
                    this.students = response.text;
                    this._broadcaster.broadcast("get_students", this.students);
                    this._broadcaster.broadcast("spinner_on", false);
                })
            }
        });
    }

    uiEvents2(event_data: any) {
        this._broadcaster.broadcast("spinner_on", true);
        switch (event_data.action) {
            case 'student_update':
                this._o365SecurityService.patch(event_data.url, event_data.http_data, this.persistsId)
                    .subscribe((response: any) => {
                        this.currentStudent = event_data.temp_data.values[0];
                        this._broadcaster.broadcast("spinner_on", false);
                    })
                break;
            case 'student_add':
                this._o365SecurityService.post(event_data.url, event_data.http_data, this.persistsId)
                    .subscribe((response: any) => {
                        this.currentStudent = response.values[0];
                        this._broadcaster.broadcast("spinner_on", false);
                    })
                break;
            case 'filter':
                Observable.forkJoin([this._o365SecurityService.post(event_data.url, event_data.http_data, this.nonpersistId)])
                    .subscribe((response) => {
                        if (response[0] === null) {
                            this._o365SecurityService.get(event_data.filtered_url, "", "", "", this.nonpersistId).subscribe((response: any) => {
                                this.students = response.text;
                                this._broadcaster.broadcast("get_students", this.students);
                                this._broadcaster.broadcast("spinner_on", false);
                            })
                        }
                    })
                break;
            case 'All':
                let excelUrl1 = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/range/visibleView';
                this._o365SecurityService.get(excelUrl1, "", "", "", this.persistsId).subscribe((response: any) => {
                    this.students = response.text;
                    this._broadcaster.broadcast("get_students", this.students);
                    this._broadcaster.broadcast("spinner_on", false);
                })
                break;
            case 'chart':
                Observable.forkJoin([this._o365SecurityService.post(event_data.url, event_data.http_data, this.nonpersistId)])
                    .subscribe((response) => {
                        if (response[0] !== null) {
                            this._o365SecurityService.get2(event_data.filtered_url, "", "", "", this.nonpersistId).subscribe((response: any) => {
                                this.chartBase64 = response.value;
                                this._broadcaster.broadcast("get_chart_image", this.students);
                                this._broadcaster.broadcast("spinner_on", false);
                            })
                        }
                    })
                break;
            default:
                'default'
        }
    }
    //todo:move to upper class
    getFilterData(filter_params: Filter_Params): Filter_Data {
        let obj_filter_data: Filter_Data = {
            filter_criteria: {
                "criteria": {
                    "filterOn": "custom",
                    "criterion1": "=" + this.currentUser.userPrincipalName + ""
                }
            },
            filter_url: this.Stringformat(this.graphConstat.excel_endpoint + "Sheet{0}/tables/{1}/columns(id='{2}')/filter/apply", filter_params.worksheet_index, filter_params.table_index, filter_params.column_index),
            visiable_url: this.Stringformat(this.graphConstat.excel_endpoint + "Sheet{0}/tables/{1}/range/visibleView", filter_params.worksheet_index, filter_params.table_index)
        }
        return obj_filter_data;
    }

    getUser(id: string) {
        let selectedUser: MSGraph.User
        this.instructors.filter((user: MSGraph.User) => {
            if (user.id === id) {
                selectedUser = user;
            }
        })
        return selectedUser;
    }

    registerEvents() {
        // this.subscription_login_success = this._broadcaster.on<string>('login_success')
        //     .subscribe((result: any) => {
        //         if (Object.keys(this.currentUser).length === 0 && this.currentUser.constructor === Object) {
        //             this.get("me", "/me", "", "", "", "");
        //         }
        //     })

        // this.subscription_profile_photo = this._broadcaster.on<string>('profile_photo')
        //     .subscribe((result: any) => {
        //         this.currentStudent.url = result.url;
        //     })
    }

    login() {
        if (InsideOffice.isOffice) {
            this._o365SecurityService.login2();
        }
        else {
            this._o365SecurityService.login();
        }

    }

    logOut() {
        this.instructors.length = 0;
        this._o365SecurityService.logout();
    }

    //move this to utility
    setDefaultPhoto() {
        this.instructors.filter((user) => {
            user.url = 'assets/libs/download.jpeg';
        })
    }

    Stringformat(str: string, param1?: string, param2?: string, param3?: string, param4?: string) {
        var s = arguments[0];
        for (var i = 0; i < arguments.length - 1; i++) {
            var reg = new RegExp("\\{" + i + "\\}", "gm");
            s = s.replace(reg, arguments[i + 1]);
        }
        return s;
    }

    graphConstat = {
        excel_endpoint: "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/",
        create_session: 'createSession',
        excel_create_session: "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/createSession",
        me: '/me',
        users: '/users',
        eq_filter: '{0} eq {1}',
        excel_filter: "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/columns(id=' {0} ')/filter/apply",
        excel_visible_view: "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/range/visibleView",
        admin_navs: [
            { name: 'Home', route: 'home' },
            { name: 'Instructors', route: 'instructors' },
            { name: 'Locations', route: 'locations' },
            { name: 'Register', route: 'register' },
            { name: 'Students', route: 'students' }
        ],
        user_navs: [
            { name: 'Home', route: 'home' },
            { name: 'Register', route: 'register' }
        ],
        admin_email: 'jay@officeintegrator.com'
    }

    parseHomeResponse(response: any, index: number) {
        if (index === 0) {
            this.currentUser = response;
            if (this.currentUser.userPrincipalName === this.graphConstat.admin_email) {
                this.navItems = this.graphConstat.admin_navs;
            }
            else {
                this.navItems = this.graphConstat.user_navs;
            }
        }
        else if (index === 1) {
            this.persistsId = response.id;
        }
        else {
            this.nonpersistId = response.id;
            this._broadcaster.broadcast("display.name", this.currentUser.displayName);
            this._broadcaster.broadcast("navItems", this.navItems);
        }
    }

    parseRegisterResponse(response: any, index: number) {
        if (index === 0) {
            this.instructors = response.value;
        }
        else if (index === 2) {
            if (response.values.length > 1) {
                this.studentSchema = response.values[0];
                this.currentStudent = response.values[1];
                this._broadcaster.broadcast("instructorReady", this.instructors);
                this._broadcaster.broadcast("get_student_data", this.currentStudent);
            }
        }
    }

}



